  height: 50px;
  opacity: 0.65;
  position: absolute;
  text-align: center;
  padding: 0;
  border: 0;
}
.glide__arrow:after {
  content: "";
  display: inline-block;
  border-right: 1px solid #000;
  border-bottom: 1px solid #000;
  width: 17px;
  height: 17px;
  position: relative;
  top: 3px;
  -webkit-transition: border-right 0.35s ease, border-left 0.35s ease;
  -moz-transition: border-right 0.35s ease, border-left 0.35s ease;
  -ms-transition: border-right 0.35s ease, border-left 0.35s ease;
  -o-transition: border-right 0.35s ease, border-left 0.35s ease;
  transition: border-right 0.35s ease, border-left 0.35s ease;
}
