  border-right: 1px solid #005bff;
  border-bottom: 1px solid #005bff;
}
.glide__arrow--left {
  left: 0;
}
.glide__arrow--left:after {
  transform: rotate(-225deg);
  left: 4px;
}
.glide__arrow--right {
  right: 0;
}
